import { logger } from "../../../utils/log/index";

export class BaseRequests {
  constructor(public shouldPrintRequest: boolean) {}

  public async printRequest(buildRequest: any): Promise<void> {
    if (this.shouldPrintRequest) {
      logger.error(buildRequest);
    }
  }

  public async execPromiseAll(execPromises: any) {
    const [response] = await Promise.all(execPromises);
    return response;
  }
}
